var _this
export default {
	data() {
		return {
			user:{},       //用户的信息
			avatarUrl:'',  //用户的头像
			nickName:''     //用户的昵称
		}
	},
	onLoad() {
		_this = this;
		uni.getStorage({
			key:'user',
			success:(res) => {
				_this.user = res.data





				   _this.avatarUrl = res.data.qqAvatarUrl,
				   _this.nickName = res.data.qqNickName

			},
			fail:() => {
				uni.reLaunch({
					url:'../login/login'
				})
			}
		})
	},
	components:{
		
	},
	
	methods: {
		logout:function(){
			_this = this;
			try {
			    uni.clearStorageSync();
				uni.reLaunch({
					url:'../login/login'
				})
			} catch (e) {
			   uni.showToast({
			   	 title:"网络错误",
				 icon:"loading",
				 duration:500
			   })
			   console.log("退出登录失败",e.message)
			}
		}
	}
}
